body {
  font-family: Lucida Sans, Lucida Sans Regular, Lucida Grande, Lucida Sans Unicode, Geneva, Verdana, sans-serif;
}

.wrapper {
  width: 400px;
  height: 650px;
  background-color: #618685;
  padding: 10px 20px;
}

.country-list {
  width: 360px;
  margin-bottom: 0;
  padding: 0;
  line-height: 1.5;
}

.country-list > li {
  background-color: #d5f4e6;
  border-radius: 5px;
  margin-bottom: 5px;
  padding: 7px;
}

.country-info {
  width: 360px;
}

ul {
  padding-left: 0;
  list-style: none;
}

img, h2 {
  display: inline;
}

.country-card {
  background-color: #d5f4e6;
  border-radius: 5px;
  padding: 10px;
}

